body, html {
    height: 100%;
    width: 100%;
    padding: 0;
    margin: 0;
    overflow: hidden;
    color: #333;
    font-size: 12px;
}
ul,li{
    list-style: none;
}
a{
    color:#fff;
    text-decoration:none;
}
.main {
    height: 100%;
    background-color: #fff;
    font-size: 0;
}

.step {
    transform-origin: -50% -50%;
    font-size: 12px;
    height: 100%;
    width: 100%;
    padding: 0;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    transition: all 1s;
}

.step1 {
    background-color: #22B573;
    line-height: 100%;
}

.step2 {
    background-color: #1889C5;
}

.step3 {
    background-color: #9C58B6;
}

.step4 {
    background-color: #26B1B6;
}

.step5 {
    background-color: #129DE7;
}

@keyframes fallsDown {
    0% {
        opacity: 1;
        transform: rotateZ(0deg) translate(-50%, -50%)
    }
    30% {
        transform: rotateZ(50deg) translate(-50%, -50%)
    }
    50% {
        transform: rotateZ(40deg) translate(-50%, -50%)
    }
    70%, 80% {
        transform: rotateZ(45deg) translate(-50%, -50%)
    }
    83% {
        transform: rotateZ(45deg) translate(-40%, -40%)
    }
    85% {
        transform: rotateZ(45deg) translate(-30%, -30%)
    }
    88% {
        transform: rotateZ(45deg) translate(-20%, -20%)
    }
    90% {
        transform: rotateZ(45deg) translate(0, 0)
    }
    100% {
        transform: rotateZ(45deg) translate(2000px, 2000px);
        opacity: 0;
    }
}

@keyframes getBackHere {
    100% {
        transform: rotateZ(0deg) translate(-50%, -50%);
        opacity: 1
    }
    30% {
        transform: rotateZ(45deg) translate(-10%, -10%)
    }
    0% {
        transform: rotateZ(45deg) translate(2000px, 2000px);
        opacity: 0
    }
}

.fallsDown {
    animation: fallsDown 1s linear forwards;
}

.getBackHere {
    animation: getBackHere .4s linear forwards;
}

.main {
    position: relative;
}

.next, .prev {
    z-index: 99;
    position: absolute;
    right: 40px;
    top: 14%;
    height: 80px;
    width: 80px;
    cursor: pointer;
    font-size: 80px;
    line-height: 80px;
    text-align: center;
    color: #fff;
}

.prev {
    display: none;
}

.next {
    top: 75%;
}

.step1>div {
    font-size: 100px;
    color: white;
    text-align: center;
    width: 100%;
    transform: translateY(-100%);
    top: 0%;
    position: absolute;
    transition: all 1s;
    opacity: 0;
}

.step1>div>p {
    margin: 0 auto;
    text-align: center;
    width: 100%;
    display: block;
    line-height: 100px;
    ;
}

.step1 .fadeIn {
    top: 50%;
    transform: translateY(-50%);
    opacity: 1;
}

.container {
    display: flex;
    justify-content: space-between;
    width: 70%;
    margin: 0 auto;
    margin-top: 40px;
    align-items: center;
}

.container-small {
    color: #fff;
    display: flex;
    justify-content: space-between;
    width: 50%;
    margin: 0 auto;
    margin-top: 40px;
    align-items: top;
}
.container-small>div {
    font-size: 16px;
    flex:1;
    text-align: center;
}
.container-small>div:last-child{
    text-align: left;
}
.container-small>div  >ul > li{
    margin-top: 10px;
}
.container>div {
    font-size: 12px;
    width: 25%;
    text-align: center;
}

.container .image_box {
    height: auto;
    overflow: hidden;
    border-radius: 50%;
    flex-basis: 270px;
}

.container .image_box img {
    width: 100%;
    height: 110%;
}

.container .item img {
    display: inline-block;
    border-radius: 50%;
    transition: .8s all;
    width: 28%;
}

.container .item a {
    overflow: hidden;
}

.container .item img:hover {
    transform: rotate(360deg)
}

@media screen and (max-width:1000px) {
    .step {
        border-radius: 0;
        width: 100%;
    }
    .prev, .next {
        right: 14px;
        font-size: 60px;
    }
    .container {
        width: 100%;
    }
    .step1>div>p {
        font-size: 60px;
    }
    .next {
        top: 85%;
    }
}